export default {"name":"白鸡冠茶","img":"\\wulongcha\\baijiguancha\\img_20191122105854xtbvr0.png","desc":["白鸡冠是武夷山四大名丛之一。无性系。灌木型，中叶类，晚生种。二倍体。在慧苑岩火焰峰下外鬼洞和武夷山公祠后山的茶树，芽叶奇特，叶色淡绿，绿中带白，芽儿弯弯又毛绒绒的，那形态就像白锦鸡头上的鸡冠，故名白鸡冠。"],"menus":[{"name":"白鸡冠茶茶叶历史","content":[{"text":"白鸡冠是武夷山“四大名丛”之一，由于它的产量和种植面 积较少，而且年产量低，所以与其它相比，它就显得非常的珍 贵。白鸡冠最大的品种特征是嫩叶淡黄，香气清锐，且岩韵悠长。白鸡冠茶叶名称的由来与干茶叶没有多大的关系，主要是它 鲜叶的形状酷似鸡冠，因而得名。每当春季来临时，白鸡冠茶树 的鲜叶绿中带白，特别是幼叶薄软如棉，色浅绿微黄，叶面开展 欠光泽，春梢顶芽微弯，茸毛显露，与绿色的老叶形成明显的双 色层，乍一看，就像白锦鸡头上的鸡冠，美丽奇特极了。白鸡冠生长于武夷山的止庵白蛇洞口，树状很小，但却生长 得很茂盛，但是真茶苗繁育成活率却很低，植株生长得也很缓 慢，所以这才显得白鸡冠茶会异常珍贵。白鸡冠的香气不仅有茶 本身的香味还兼有菇菌类的扬香，茶汤刚一入口后，便有清炖的 高汤味，此美令人垂涎。白鸡;H的米摘时间是在每年的4月下旬，鲜叶米摘是按照 “小至中开面”的标准来采的，采摘时要以二叶或三叶为主，不 宜偏嫩或偏老采。相传白鸡冠名早在明代就已经被叫开了，主要分布在福建省 武夷山隐屏峰蝙蝠洞，在武夷山慧苑岩火焰峰下的外鬼洞也有此 茶树。自20世纪80年代以来，武夷山市已扩大了该类茶叶的栽培 与繁殖。"}]},{"name":"白鸡冠茶茶叶特点","content":[{"text":"白鸡茶植株中等大小，树姿半开张，分枝较密，叶片呈稍上斜状着生。叶长椭圆或椭圆形，叶色深绿，有光泽，叶面微隆起，叶缘微波，叶身内折或平，叶尖渐尖或稍钝，叶齿稍钝浅密，叶质较厚脆。"},{"src":"\\wulongcha\\baijiguancha\\img_20191122105854ni0ugi.jpg"},{"text":"白鸡冠芽叶黄白色，茸毛少，节间短，一芽三叶百芽重57.0g。花冠直径3.3cm，花瓣7瓣，子房茸毛多，花柱3裂。"}]},{"name":"白鸡冠茶加工工艺","content":[{"text":"1、萎凋："},{"text":"将白鸡冠茶青进行日光萎凋、或将茶青均匀摊铺在萎凋槽上加热萎凋。"},{"src":"\\wulongcha\\baijiguancha\\img_20191122105854dxjmrg.jpg"},{"text":"2、做青："},{"text":"萎凋结束后进入做青程序。如果是日光萎凋处理的白鸡冠茶青，将萎凋好的白鸡冠茶青均匀摊铺到萎凋槽上，进入做青程序。如果是在萎凋槽上加热萎凋，萎凋结束后直接转入做青程序。"},{"text":"做青过程中，静置发酵(温度过低时必须提高室温)、通风降温使青叶复苏这两个环节反复进行。静置发酵时(室温过低时还必须加温)，叶片水分蒸发，内含物浓度增大，内含物反应激烈起来，使白鸡冠茶青苦涩物质转化，香气慢慢形成;通风降温时，白鸡冠茶青内含物化学反应强度降低，氧化作用相对减缓，此时叶柄叶脉中的水分慢慢扩散至叶片，白鸡冠茶青又逐渐恢复生命活性，白鸡冠茶青内含物的反应转化慢下来，以控制做青的过程。这样的过程使水分和茶叶内含物在叶片和茶梗中的运转(俗称走水)，同时，白鸡冠茶青内含物发生一系列生物化学变化，促使白鸡冠茶青茶青苦涩物质转化、使香气形成和显露。"},{"text":"根据做青的进展，适时使用所述刺针板水平按压萎凋槽上的白鸡冠茶青，在白鸡冠茶青叶片上形成均匀分布微小针眼，使白鸡冠茶青叶片部分细胞破坏，从而促进酶促氧化作用。形成“绿叶红星点~一片绿叶红星点点”，以达成武夷岩茶特有的汤色特点。使用刺针板水平按压萎凋槽上的白鸡冠茶青的次数，根据白鸡冠茶青叶片部分细胞破坏的情况和酶促氧化作用的进展来确定，一般在做青结束时使红星点面积，占叶片比例为15~30%为宜。"},{"text":"3、炒青："},{"text":"将完成做青工序的白鸡冠茶青杀青。"},{"text":"4、揉捻："},{"text":"对完成杀青工序的白鸡冠茶青进行揉捻。"},{"text":"5、走水烘干："},{"text":"将揉捻好的白鸡冠茶青进行烘干，得到高端手工白鸡冠茶毛茶。"},{"text":"6、精制："},{"text":"对高端手工白鸡冠茶毛茶进行捡剔，除去茶梗、黄片后，进行多次烘培。"}]},{"name":"白鸡冠茶储藏方法","content":[{"text":"1.在家里存放白鸡冠茶，要放在干燥、避光、密封、不通风、没有异味的地方"},{"text":"2.现在很多白鸡冠茶都是包装好的，有些用塑料袋包装，有些用铝箔袋包装，还有些是用铝箔袋抽真空包装好。用塑料袋包装的不宜长放，最好尽快饮用。用铝箔袋包装的可以略微降低存放标准。"},{"text":"3.白鸡冠茶不要放在厨房或者有香皂、樟脑丸、调味品的柜子里，以免吸收异味。"},{"text":"4.容器选择没有异味的瓷罐、铁罐、竹盒、木盒、瓦坛子等，尽量装满，加盖密封后置于电冰箱内冷藏。"}]},{"name":"白鸡冠茶冲泡方法","content":[{"text":"1、温壶温杯：可采用盖碗或\n        紫砂壶\n        ，先用开水烫盖碗(壶)、茶海及茶杯，使器具达到一定的温度，此举有利于后续泡茶时茶性更好地发挥，并可清洁杀菌、消除\n        茶具\n        的异味。"},{"src":"\\wulongcha\\baijiguancha\\img_20191122105854g1k0a7.jpg"},{"text":"2、温润泡：将约7-8克的白鸡冠茶叶放入盖碗(壶)，冲入开水只需浸没茶叶即可，快速倒出茶汤至茶海，再次烫杯。此步骤为温润泡，目的主要是唤醒茶性。"},{"text":"3、冲泡：向盖碗(壶)里再次冲入开水，需10至20秒的时间。可根据个人对茶汤浓淡的喜好，适当增减茶叶浸泡时间，即可倒出至茶海，再将茶海里的茶汤倒入品茗杯，即可品饮;下一次冲泡时间可缩短至10秒，因为此时茶叶经过两次开水冲泡已经舒展开，之后每次冲泡延长10秒即可。"}]},{"name":"白鸡冠茶功效作用","content":[{"text":"白鸡冠的主要成分是茶多酚、咖啡碱、脂多糖等，具有药理作用。饮用白鸡冠，通常会出现周身煦暖、手心出汗、排尿量大、身心通泰情况，这恰恰验证了白鸡冠作为“道茶”以茶调气、行气通脉的特殊功效。"},{"text":"我国南方湿气重，饮用白鸡冠能发汗解表，因而手心和鼻尖易出汗;咖啡碱还会刺激肾脏，促使尿液迅速排除体外，提高肾脏的滤出率，减少有害物质在肾脏中的滞留时间，十分适合糖尿病、高血压患者饮用;富含的咖啡碱还可以排除尿液中的过量乳酸，有助于使人尽快消除疲劳。这也恰恰验证了白鸡冠作为“道茶”以茶调气、行气通脉的特殊功效。"},{"text":"白鸡冠茶的功效：鸡冠\n        花茶\n        性凉，味甘、涩。收涩止血，止带，止痢。用于吐血、崩漏、便血、痔血、赤白带下、久痢不止。"}]},{"name":"白鸡冠茶历史故事","content":[{"text":"传说一："},{"text":"1、相传白鸡冠是宋代道教南宗五祖白玉蟾发现并培育的茶种，白玉蟾当时是武夷山止止庵道观的主持，而白鸡冠的原产地就在武夷山大王峰下止止庵道观白蛇洞。"},{"text":"2、“两腋清风起，我欲上蓬莱”，这是八百年前白玉蟾发现培植出武夷名枞白鸡冠，并在武夷山止止庵内畅饮后写下的佳句。"},{"text":"传说二："},{"text":"1、 古时候武夷山有位茶农。一日，他的岳父做生日，他就抱着家里的一只大公鸡去祝寿。一路上，太阳火辣辣的，他被炙烤得受不了啦。"},{"text":"2、走到慧苑岩附近，便把公鸡放在一棵树下，自己找了个阴凉的地方，拿下斗笠噼叭噼叭地扇起风来。"},{"text":"3、还没一袋烟工夫，忽地听到公鸡“喔”地一声惨叫。他赶忙跑过去看，一条拇指粗的青蛇从他脚边一擦而过，差点把他吓出一身冷汗。再看大公鸡，脑袋耷拉着，殷红的血从公鸡的冠上往下流一滴一滴正落在旁边的一棵茶树根上。"},{"text":"4、那茶农气得两眼冒火，恨得咬牙切齿，但又无可奈何，他只好在茶树下扒了个坑将大公鸡埋了，垂头丧气空着手去岳父家祝寿。"},{"text":"5、也不怎的，慧苑岩附近的这棵茶树打那以后，长势物别旺盛，一股劲地往上窜，枝繁叶茂，比周围的茶树高一截。"},{"text":"6、那满树的叶子也一天天地由墨绿变成淡绿，由淡绿变成淡白，几丈外就能闻到它那股浓郁浓郁的清香。"},{"text":"7、制成的茶叶，颜色也与众不同，别的茶叶色带褐色，它却是在米黄中呈现出乳白色;泡出来的茶水晶亮晶亮的，还没到嘴边就清香扑鼻;啜一口，更觉清凉甘美，连那茶杆嚼起来也有一股香甜味，据说喝了还能治病。这茶树就是武夷名丛“白鸡冠”。"},{"text":"传说三："},{"text":"1、明朝年间，武夷山慧苑寺有一位僧人，名圆慧。他为人善良，待人接物总是笑脸相迎，人们称之为“笑脸罗汉”。笑脸罗汉除了念经参禅外，还管理着一片茶园。"},{"text":"2、一天清晨，笑脸罗汉早课完毕，立即荷锄赴火焰岗茶园锄草。当他锄至岩边的茶畦时，突然从山岗上传来一阵锦鸡惨叫的声。"},{"text":"3、原来一只老鹰要捕捉幼小的锦鸡，一只母白锦鸡正与它舍命对抗，保护着小锦鸡。笑脸罗汉跑了过去，赶跑了老鹰，却为时已晚，母白锦鸡几经死了。"},{"text":"4、他顺手把母白锦鸡埋在一株茶树下。"},{"text":"5、第二年春天，笑脸罗汉发现那一株茶树长得与众不同。叶片是白色的，往上向内卷起，形似鸡冠，在阳光照耀下，闪闪发光，他把白鸡冠茶树的叶片采下，精制成茶叶，竟满室生香;冲泡后的白鸡冠散发着阵阵清幽的兰花香气，直透肺腑，令人心旷神怡，饮后更是满口生津，回味有余香。笑脸罗汉视白鸡冠为珍宝，用锡罐收藏起来。"},{"text":"6、有一年夏天，建宁知府带着眷属来武夷山游览，途径慧苑寺时，他的公子突然腹痛难忍，请笑脸罗汉诊病。他把脉后从锡罐中取出少许白鸡冠冲泡，请公子服下。"},{"text":"7、没多久公子病愈，知府大人询问是何神丹妙药?笑脸罗汉答道：“此乃‘白鸡冠’茶。”知府奏献此茶，皇帝尝后认为白鸡冠是茶中上品，每年封制进贡，一直延续至清代。从此一传十，十传百，白鸡冠茶名闻遐迩。"}]},{"name":"白鸡冠茶鉴别好茶","content":[{"text":"香型：熟香"},{"text":"口感：香气高爽纯正，滋味浓醇，回味甘鲜。"},{"text":"汤色：呈橙黄色"}]}]}